<template>

  <div>
    <h3>合同模板信息编辑</h3>
    <Divider orientation="left">信息填写</Divider>
    <Row>
      <Col :xs="24" :sm="24" :md="24" :lg="24">
        <Form :model="formData" :label-width="100">
          <FormItem label="模板ID">
            <Input v-model="formData.template_no" placeholder="请填写模板编号"></Input>
          </FormItem>
          <FormItem label="模板名称">
            <Input v-model="formData.name" placeholder="请填写模板名称"></Input>
          </FormItem>
          <FormItem label="模板备注">
            <Input v-model="formData.remark" placeholder="请填写模板备注"></Input>
          </FormItem>
          <FormItem label="状态">
            <Select v-model="formData.status">
              <Option label="不可用" :value="0"></Option>
              <Option label="可用" :value="1"></Option>
            </Select>
          </FormItem>
          <FormItem>
            <rich-view v-model="formData.rich_text"></rich-view>
          </FormItem>
          <FormItem label="自定义变量">
            <Row v-if="formData.content" v-for="(item,index) in formData.content" v-bind:key="index"
                 style="margin-bottom: 20px">
              <Col style="text-align: center" :span="2">
                名称
              </Col>
              <Col :span="8">
                <Input v-model="item.name"
                       placeholder="请输入变量名称"></Input>
              </Col>
              <Col style="text-align: center" :span="2">
                替换符
              </Col>
              <Col :span="8">
                <Input v-model="item.placeholder"
                       placeholder="请输入变量替换符"></Input>
              </Col>
              <Col style="text-align: center" :span="4">
                <Button type="error" @click="deleteIndex(index)">删除</Button>
              </Col>

            </Row>
            <Col :span="22" style="text-align: center">
              <Button type="primary" @click="addContent">新增自定义变量</Button>
            </Col>
          </FormItem>
          <FormItem>
            <Button type="success" long @click="submit">提交</Button>
          </FormItem>

        </Form>
        <Spin size="large" fix v-if="load"></Spin>
      </Col>

    </Row>
  </div>
</template>

<script>
import api from './api'

export default {
  name: 'edit',
  data () {
    return {
      formData: {},
      load: false
    }
  },
  mounted () {
    if (this.$route.query.id != null) {
      this.loadData()
    }
  },
  methods: {

    loadData () {
      this.load = true
      api.detail({ id: this.$route.query.id }, res => {
        this.load = false
        this.formData = res.data.data
        this.formData.space_id = this.formData.space.id
      }, () => {
        this.load = false
      })
    },
    addContent () {
      if (this.formData.content == null) {
        this.formData.content = []
      }
      this.formData.content.push({ name: '', placeholder: '' })
    },
    deleteIndex (index) {
      this.formData.content.splice(index, 1)
    },
    submit () {
      this.load = true
      api.save(this.formData, () => {
        this.load = false
        this.$router.push({ name: 'contract_template' })
      }, () => {
        this.load = false
      })
    }
  }
}
</script>

